import React, { useState, useEffect, useLayoutEffect } from 'react'

import { Box, Typography, Drawer, Avatar, IconButton, Toolbar, List, Divider, Icon, Modal, Grid, Switch } from '@mui/material'
import Button from '@mui/material/Button';
import { Footer, Navbar } from "../components";

import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import { useParams } from 'react-router-dom';
import Alert, { AlertProps } from '@mui/material/Alert';

import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';


const CheckError = () => {
    const [name, setName] = useState('');
    const handleChangeName = (e) => {
        setName(e.target.value)
    }

    const [type, setType] = useState('Bt');
    const handleChangeType = (e) => {
        setType(e.target.value)
    }

    const [file, setFile] = useState('');
    const [filePreview, setFilePreview] = useState();
    const [error, setError] = useState('');

    const handleFileChange = (e) => {
        let selectedFile = e.target.files[0];

        // console.log(selectedFile.type)
        // let fileType = ['image/jpeg', 'image/png'];

        if (selectedFile) {
            //Type l jpg => sn phm khng li
            if (selectedFile.type === 'image/jpeg') {
                setFilePreview(URL.createObjectURL(e.target.files[0]));
                setFile(selectedFile)
                setError('');
            }
            //Type l png => sn phm li
            else if (selectedFile.type === 'image/png') {
                setFilePreview(URL.createObjectURL(e.target.files[0]));
                setFile(selectedFile)
                setError('Sn phm ca bn b li');
            }
            //Import sai kiu (khng phi png/jpg)
            else {
                setError('Vui lng chn ng nh dng file hnh nh');
                setFile('')
            }
        }
        else {
            setError('Vui lng chn file!');
        }
    }

    return (
        <>
            <Navbar />
            <div className="container my-3 py-3">
                <h1 className="text-center">Kim li</h1>
                <hr />
                <h5 className="text-center fs-5 weight-light">Qu khch hng vui lng kim li ti y.</h5>
                <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', width: '100%' }}>
                    <Box sx={{ margin: '5px', width: '30%' }}>
                        <Typography sx={{ width: '100%', marginY: '10px', color: 'gray' }}>Chn loi sn phm</Typography>
                        <FormControl sx={{ width: '100%' }}>
                            <Select
                                value={type}
                                onChange={handleChangeType}
                                defaultValue={'Bt'}
                                size='small'
                            >
                                <MenuItem value={'Bt'}> Bt </MenuItem>
                                <MenuItem value={'S tay'}> S tay </MenuItem>
                                <MenuItem value={'K nim chng'}> K nim chng </MenuItem>
                                <MenuItem value={'Ly'}> Ly </MenuItem>
                                <MenuItem value={'Ti vi'}> Ti vi </MenuItem>
                                <MenuItem value={'Huy chng'}> Huy chng </MenuItem>
                                <MenuItem value={'Mc kha'}> Mc kha </MenuItem>
                                <MenuItem value={'Bnh gi nhit'}> Bnh gi nhit </MenuItem>
                            </Select>
                        </FormControl>
                        <Typography sx={{ width: '100%', marginY: '10px', color: 'gray' }}>Tn sn phm</Typography>
                        <TextField
                            required
                            value={name}
                            onChange={handleChangeName}
                            size='small'
                            sx={{ marginRight: '10px', width: '100%' }}
                            id="outlined-basic"
                            variant="outlined"
                            placeholder='Nhp tn sn phm'
                        />
                        <Box sx={{ mt: '15px', display: 'flex', flexDirection: 'column' }}>
                            <Typography sx={{ width: '100%', color: 'gray' }}>File hnh nh ca sn phm</Typography>
                            <Button
                                sx={{
                                    backgroundColor: '#008272', color: 'white', fontSize: '16px', py: '6px',
                                    textTransform: 'initial', px: '20px',
                                    '&:hover': {
                                        backgroundColor: '#008272',
                                        color: 'white'
                                    },
                                }}
                                component="label"
                            >
                                Thm file
                                <input
                                    onChange={handleFileChange}
                                    type="file"
                                    hidden
                                />
                            </Button>
                            {file !== '' && error === '' ? <Box sx={{ mt: '15px' }}>
                                <img src={filePreview} alt='Error image' width='100%' />
                                <Typography sx={{ width: '100%', marginY: '10px', color: 'gray' }}>Tnh trng sn phm</Typography>
                                <Alert severity="success">Sn phm ca bn khng b li</Alert>
                            </Box> : null}
                            {error !== '' && error === 'Vui lng chn ng nh dng file hnh nh' ? <Box sx={{ mt: '15px' }}>
                                <Alert severity="error">{error}</Alert>
                            </Box> : null}
                            {error !== '' && error === 'Sn phm ca bn b li' ? <Box sx={{ mt: '15px' }}>
                                <img src={filePreview} alt='Error image' width='100%' />
                                <Typography sx={{ width: '100%', marginY: '10px', color: 'gray' }}>Tnh trng sn phm</Typography>
                                <Alert severity="error">{error}</Alert>
                            </Box> : null}
                        </Box>
                    </Box>
                </Box>
            </div>
            <Footer />
        </>
    )
}

export default CheckError;
