import React from "react";
import { Footer, Navbar } from "../components";
const ContactPage = () => {
  return (
    <>
      <Navbar />
      <div className="container my-3 py-3">
        <h1 className="text-center">Lin h vi chng ti</h1>
        <hr />
        <h5 className="text-center fs-5 weight-light">Hy  li thng tin lin lc v km li nhn nh ca bn </h5>
        <div class="row my-4 h-100">
          <div className="col-md-4 col-lg-4 col-sm-8 mx-auto">
            <form>
              <div class="form my-3">
                <label for="Name">H v tn</label>
                <input
                  type="email"
                  class="form-control"
                  id="Name"
                  placeholder="Nhp h v tn bn "
                />
              </div>
              <div class="form my-3">
                <label for="Email">Email</label>
                <input
                  type="email"
                  class="form-control"
                  id="Email"
                  placeholder="name@gmail.com"
                />
              </div>
              <div class="form  my-3">
                <label for="Password">Li nhn nh</label>
                <textarea
                  rows={5}
                  class="form-control"
                  id="Password"
                  placeholder="Nhp li nhn nh"
                />
              </div>
              <div className="text-center">
                <button
                  class="my-2 px-4 mx-auto btn btn-dark"
                  type="submit"
                  disabled
                >
                  Gi
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default ContactPage;
