import React from 'react'
import { Footer, Navbar } from "../components";
import { Link } from 'react-router-dom';
const Register = () => {
    return (
        <>
            <Navbar />
            <div className="container my-3 py-3">
                <h1 className="text-center">ng k</h1>
                <hr />
                <div class="row my-4 h-100">
                    <div className="col-md-4 col-lg-4 col-sm-8 mx-auto">
                        <form>
                            <div class="form my-3">
                                <label for="Name">H v tn</label>
                                <input
                                    type="email"
                                    class="form-control"
                                    id="Name"
                                    placeholder="in h v tn"
                                />
                            </div>
                            <div class="form my-3">
                                <label for="Email">a ch Email</label>
                                <input
                                    type="email"
                                    class="form-control"
                                    id="Email"
                                    placeholder="name@gmail.com"
                                />
                            </div>
                            <div class="form  my-3">
                                <label for="Password">Mt khu</label>
                                <input
                                    type="password"
                                    class="form-control"
                                    id="Password"
                                    placeholder="Nhp mt khu "
                                />
                            </div>
                            <div className="my-3">
                                <p>Bn  c ti khon? <Link to="/login" className="text-decoration-underline text-info">ng nhp  y</Link> </p>
                            </div>
                            <div className="text-center">
                                <button class="my-2 mx-auto btn btn-dark" type="submit" disabled>
                                    ng k
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Register